.region{
  font-size: 2rem;
  padding-top: 5vh;
}

//===Navbar===

nav{
  min-height: 10vh;
  width: 100%;
  box-shadow: 0 0 5px 0;
  background: linear-gradient(90deg, rgb(3, 52, 77) 0%, rgb(14, 81, 157) 56%);
}

.search-container{
  padding-top: 1.2rem;
  margin-left: 2em;
}

.search{
  font-size: 1em;
  font-weight: bold;
  padding: .53em .45em;
  border-radius: 2em 0 0 2em;
  border: 1px solid rgba(0, 0, 0, 0.295);
  background: lightgray;
}

.search-btn{
  margin: .3em 0 .5em 0;
  padding: .31em .25em;
  border-radius: 0 2em 2em 0;
  border: 1px solid rgba(0, 0, 0, 0.295);
  font-size: 1.2em;
  color: black;
  background: #0342a0;
}

//===Weather component===

.weather-container{
  min-height: 70vh;
}

.weather{
  position: inherit;
  border-radius: 10px;
  width: 340px;
  height: 190px;
  margin: 0 auto;
  margin-top: 10vh;
  padding: .8em 0 0 .5em;
  display: flex;
  background: #a9a9a936;
}

.temp{
  max-width: 140px;
  margin-right: 1em;
  h2{
    padding-bottom: .3em;
  }
}


.line{
  margin-top: .5em;
  padding: 1.5rem 0;
  border-radius: 10px;
  border: 1px solid black;
  max-height: 160px;
}


div{
  h3{
    font-size: 1.4rem;
    padding: .8em 0;
  }
  p{
    
    padding-left: 1em;
    font-size: 1.2rem;
  }
}

@media (min-width: 980px) {
  .region{
    padding-top: 10vh;
  }
  .weather-container{
    margin-top: 10vh;
    display: flex;
    flex-direction: row;
  }
  .temp{
    margin-right: .5em;
  }
}

//===Footer===

footer{
  position: inherit;
  min-height: 30vh;
  margin-top: 10vh;
  padding-top: 13vh;
  justify-content: center;
  background: linear-gradient(90deg, rgb(2, 40, 61) 0%, rgb(2, 40, 83) 56%);
}